.container {
  width: 730px;
}


.form-label {
  margin-bottom: 0.5rem;
  display: flex;
  justify-content: center;
}

.btn {
  background-color: white;
  color: black;
  margin: 5px 5px;
}


.h1 {
  font-size: 30px;
  font-weight: 600;
}

.heading1 {
  font-size: 30px;
  font-weight: 600;
  text-align: center;
}


.para {
  font-size: medium;
  font-weight: 600;
}


#dark {
  color: black;
  font-weight: 900;
}

#dark:hover {
  color: white;
  background-color: black;
}


#drop {
  width: 100%;
  color: black;
  font-size: 18px;
  font-weight: 600;
  height: 60px;
  text-align: end;
}

#drop:hover {
  background-color: black;
  color: white;
}

.custom-dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  transform: translateY(-100%);
  display: none;
}

.show .custom-dropdown-menu {
  display: block;
}



#change {
  background-color: black;
}


